import React from 'react';
import './Reminder.css';
class Reminder extends React.Component {
    constructor(props){
        super(props);
        this.state  = {
            height: props.height,
            currMonth: props.currMonth,
            clickedDay: props.clickedDay,
            months: props.months,
            month: props.month,
            display: props.display,
            reminders: props.reminders,
        }
    }
    componentWillReceiveProps(nextProps){
        if(nextProps.height !== this.state.height){
            this.setState({
                height: nextProps.height,
            })
        }
         if(nextProps.currMonth !== this.state.currMonth){
            this.setState({
                currMonth: nextProps.currMonth,
            })
        }
        if(nextProps.clickedDay !== this.state.clickedDay){
            this.setState({
                clickedDay: nextProps.clickedDay,
            })
    
        } 
        if(nextProps.month !== this.state.month){
            this.setState({
                month: nextProps.month,
            })
        } 

    }
    componentDidMount(){

    }
    // Obsługa kliknięcia na dany dzień tygodnia

    componentDidUpdate(){

    }
    
        
    render(){
        const addReminder = this.props.add; 
        
        return(
            <div style={{width:this.state.width}} id="rem">
            
            <h3>Wydarzenia {this.state.clickedDay} {this.state.months[this.state.currMonth]}</h3>
            <ul className="reminderList">
              <li></li>
            </ul>
            <form>
            <input type="text" id="inpReminder"></input>
            <button  id="addButton" onClick={addReminder.bind(this)}>Dodaj wydarzenie</button>
            </form>
            </div>
            
        );
    }
}
export default Reminder;


