import React from 'react';

class CalendarItem extends React.Component{
 constructor(props){
     super(props);

     this.state =  {
         currYear: props.currYear,
         currYearStatic: props.currYear,
         currMonthStatic: props.currMonth,
         currMonth: props.currMonth,
         weekdays: props.weekdays,
         month: props.month,
         currDay: props.currDay,
        

        
    } 
     
 }

 // Aktualizacja daty
 componentWillReceiveProps(nextProps){
     if(nextProps.currMonth !== this.state.currMonth){
         this.setState({
             currMonth: nextProps.currMonth,
         })
     }
      if(nextProps.month !== this.state.month){
        this.setState({
            month: nextProps.month,
        })
    } 
    if(nextProps.currYear !== this.state.currYear){
        this.setState({
            currYear: nextProps.currYear,
        })
    } 
 }

 render(){
    const click = this.props.click;
     return( //Weekday labels
            <div className="calendarCont">
            {this.state.weekdays.map((weekday,i) =>{
                return(
                    <div className="CalendarItemHeader" key={i}>{weekday}</div>
                );
            })}

            {// Highlight current day
                this.state.month.map((weekday,j) =>{
                if(weekday.main){
                if(weekday.day == this.state.currDay && this.state.currMonthStatic == this.state.currMonth){
                    if(this.state.currYearStatic == this.state.currYear ){
                    return    <div className="CalendarItemCurr" key={j} onClick={click.bind(this)}>{this.state.currDay}</div> }}
                else
                return(
                    <div className="CalendarItem" key={j} onClick={click.bind(this)}>{weekday.day}</div>
                );
                }
                else{    return( // Greying out previous and next months days
                    <div className="CalendarItemSide" key={j}>{weekday.day}</div>
                );}
   
            })}

            </div>
     );     
 }
}
CalendarItem.defaultProps = {
};
export default CalendarItem;


