import React from 'react';
import CalendarItem from './CalendarItem';
import Reminder from './Reminder';
import { timingSafeEqual } from 'crypto';
import Header from './Header';

import './Calendar.css';
class Calendar extends React.Component{
    constructor(props){
        super(props);
        this.state ={
            currYear: new Date().getFullYear(),
            currMonth:new Date().getMonth(),
            currDay: new Date().getDate(),
            weekdays:['Pn','Wt','Śr','Czw','Pt','Sob','Ndz'],
            months: ['Styczeń','Luty','Marzec','Kwiecień','Maj','Czerwiec','Lipiec','Sierpień','Wrzesień','Październik','Listopad','Grudzień'],
            month: this.showMonth(new Date().getFullYear(), new Date().getMonth()),
            reminders:  JSON.parse(localStorage.getItem('reminders')) || [],
 
        }
    }
    
    
    
    showMonth(y,m){
       
        // dzien w tygodniu pierwszego dnia miesiąca
        let firstDayOfMonth = new Date(y,m,1).getDay();
        if (firstDayOfMonth == 0){
            firstDayOfMonth = 7;
        }
        // liczba dni w miesiącu
        const lastDay = new Date(y,m+1,0).getDate();
        const monthDays = [];
        const gapBack = firstDayOfMonth-1;
        const gapForth = 7- new Date(y,m+1,0).getDay();
        let gap = 0;
        for(let i = 0; i<lastDay; i=i+1){
            
            monthDays.push({
                day: i+1,
                reminder: [],
                main: true,
                //weekday: this.state.weekdays[firstDayOfMonth],
            })

        }
        // dodaj brakujące dni poprzedniego miesiąca
        for(let j = 0; j<gapBack; j++){
            gap = gapBack - (gapBack +j);
           
            monthDays.unshift({
               
              day: new Date(y,m,gap).getDate(),
              main: false,

            })
        }
        // dodaj brakujące dni następnego miesiąca
        for(let j = 0; monthDays.length<42; j++){
           
            
            monthDays.push({
               
              day: new Date(y,m,j+1).getDate(),
              main: false,

            })
        }
        while(monthDays.length < 42){
            monthDays.push({
                day: "."
            });
        }
       return monthDays;
    }

    nextMonth(){
        const reminder = document.querySelector('.reminderCont');
        if(reminder.classList.contains('open'))
        {   
            reminder.classList.remove('open','open2');
        }
            if(this.state.currMonth === 11){
                this.setState({
                    currMonth: 0,
                    currYear: this.state.currYear+1,
                })
            }
            else {
               this.setState({
                   currMonth: this.state.currMonth+1,
               })
            } 
        }
    
    prevMonth(){
        const reminder = document.querySelector('.reminderCont');
        if(reminder.classList.contains('open'))
        {   
            reminder.classList.remove('open','open2');
        }

        if(this.state.currMonth === 0){
                this.setState({
                    currMonth: 11,
                    currYear: this.state.currYear-1,
                })
            }
        else {
                this.setState({
                    currMonth: this.state.currMonth -1,
                })
            }
        }

    componentDidUpdate(prevProps,prevState){
        if(prevState.currMonth !== this.state.currMonth)
        {
     
           this.setState({
                month: this.showMonth(this.state.currYear,this.state.currMonth)
            }) 
        }
        if(prevState.clickedDay !== this.state.clickedDay){
            this.displayReminder();
        }              
    }

    componentDidMount(){
        localStorage.setItem('reminders',JSON.stringify(this.state.reminders));
        let to = document.getElementById('calendar');
        this.setState({
            width: to.getBoundingClientRect().width,
        }
         );

         function deleteButton(e){
            if(e.target.matches('button')){
                let parent = e.target.parentNode;
                let rem = parent.firstChild.data;
                let remsp = rem.replace(/\s+/g, '');
                console.log(remsp);
             let filtered = this.state.reminders.filter(reminder => {
                 if(reminder.reminder == remsp && reminder.day == this.state.clickedDay){
                    return false;
                }
                else {return true;}
            })
                this.setState({reminders: filtered})     
                localStorage.setItem('reminders',JSON.stringify(this.state.reminders));
      
                parent.parentNode.removeChild(parent);
            }

         
        }
  
         const list = document.querySelector('.reminderList');
         console.log(list);
         list.addEventListener('click', deleteButton.bind(this));      
    }

    handleClick(e){
        const reminder = document.querySelector('.reminderCont');
        if(reminder.classList.contains('open'))
        {   
           
        }
        else{
            reminder.classList.add('open');
            setTimeout(()=> { reminder.classList.add('open2');},150);
           
        }
        this.setState({clickedDay: e.target.innerHTML})
        this.displayReminder();
    }
    addReminder(e){
    e.preventDefault();
    let reminders = this.state.reminders;
    const reminder = document.getElementById('inpReminder');

 
        reminders.push({           
            day: this.state.clickedDay,
            month: this.state.currMonth,
            year: this.state.currYear,
            reminder: reminder.value,
        })
        this.setState({reminders: reminders,})
     this.state.month.forEach(month => {
         if(month.day == this.state.clickedDay && month.main === true)
         {
             month.reminder.push(reminder.value) 
         }
     })
     reminder.value = '';
     localStorage.setItem('reminders',JSON.stringify(this.state.reminders));
     this.displayReminder();
    }



    displayReminder(){
        
       
        const list = document.querySelector('.reminderList')
        let html = this.state.reminders.map(reminder => {
            if(reminder.day == this.state.clickedDay && reminder.month == this.state.currMonth && reminder.year == this.state.currYear){
                   return (`<li>${reminder.reminder} <button className="deleteButton" >x</button></li>`);
            }
        }).join('')
   
        list.innerHTML = html;
   
    }

   
    render(){
        
        return(
            <div className="main">
            <div id="calendar" className="calendar">
             <Header weekdays={this.state.months} currMonth={this.state.currMonth} currYear={this.state.currYear} prevMonth={this.prevMonth.bind(this)} nextMonth ={this.nextMonth.bind(this)}/>
             <div className="">
             <CalendarItem currMonth={this.state.currMonth} weekdays={this.state.weekdays} month={this.state.month} currDay={this.state.currDay} currYear={this.state.currYear} click={this.handleClick.bind(this)}/>
             </div>
            </div>
            <div className="reminderCont">
            <Reminder width={this.state.width} top={this.state.top} currMonth={this.state.currMonth} clickedDay={this.state.clickedDay} months={this.state.months} add={this.addReminder.bind(this)}/>
            </div>
            </div>
        );
    }
}

export default Calendar;


