import React from 'react';

import './Header.css';
class Header extends React.Component{


    render() {
        const weekdays = this.props.weekdays;
        const currMonth = this.props.currMonth;
        const currYear = this.props.currYear;
        const nextMonth = this.props.nextMonth;
        const prevMonth = this.props.prevMonth;
        return(
            
            <ul className="weekdays">
                <li className="weekday1"><button className="right" onClick={prevMonth.bind(this)}/></li>
                <li className="weekday">{weekdays[currMonth]} {currYear}</li>
                <li className="weekday1"><button className="left" onClick={nextMonth.bind(this)}/></li>
            </ul>
        );
    }
}
Header.defaultProps = {
};
export default Header;


